import './catalog.css'
import { Link } from 'react-router-dom'


export const CatalogPetitionItem = ({ petition }) => {


        return (
            <article className="petition">
                <div className="upper-container">
                    <div className="petition-img">
                        <img src={petition.image} alt={petition.image} />
                    </div>
                    <div className="petition-info-top">
                        <p>
                            {petition.signed ? petition.signed : 0}/{petition.goal} 
                        </p>
                        <p>
                            <span></span>
                            {petition.category}
                        </p>
                        {petition.showMyFirstName.checked ? (
                            <p>
                                 :{petition.authorInfo.firstName} {petition.authorInfo.lastName}
                            </p>
                        ) : (
                            <p> :{petition.authorInfo.username}</p>
                        )}
                    </div>
                </div>
                <div className="lower-container">
                    <div className="petition-info-bottom">
                        <h1>{petition.title}</h1>
                        <p>
                            <span>: </span>
                            {petition.description}
                        </p>
                    </div>
                    <Link to={`/details/${petition._id}`} className="btn-details">
                         
                    </Link>
                </div>
            </article>
        );
    }
        


